.Home{margin:0 auto;display:flex;height:100vh;align-items:center;justify-content:center}.Home *{font-size:3.2vw;text-transform:uppercase;color:gray}.Home ul{display:flex}.Home .menu{flex-direction:column}.Home .menu a{text-decoration:none}.Home .menu a:first-child{margin-bottom:5vw}.Home .small-btn-div{display:flex}@media (max-device-width: 1100px){.Home *{font-size:6vw}}

.remark{background:#000;border-radius:5px;margin:0 auto;padding:1em 1.5em;color:var(--red);font-size:.9vw;padding:1.2vw}.remark:hover{color:var(--red)}.flip-card{background-color:transparent;width:auto;height:100%}.flip-card-inner{position:relative;width:100%;height:100%;text-align:center;transition:-webkit-transform 0.6s;transition:transform 0.6s;transition:transform 0.6s, -webkit-transform 0.6s;-webkit-transform-style:preserve-3d;transform-style:preserve-3d;box-shadow:0 4px 8px 0 rgba(0,0,0,0.2)}.flip-card:hover .flip-card-inner{-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.flip-card-front,.flip-card-back{border-radius:15px;position:absolute;width:100%;height:100%;-webkit-backface-visibility:hidden;backface-visibility:hidden;display:flex;flex-direction:column;align-items:center;justify-content:center}.flip-card-front{background-color:lightgrey;color:grey}.flip-card-back{background-color:var(--red);color:white;-webkit-transform:rotateY(180deg);transform:rotateY(180deg)}.play-pannel{display:flex;flex-direction:column;color:grey;width:100vh;justify-content:center;margin:0 auto;position:relative}.play-pannel .top-div{align-items:center;display:flex;justify-content:space-around;position:relative;margin-bottom:1vw}.play-pannel .top-div .cards-div{display:flex;align-items:center}.play-pannel .play-zone{width:100%;flex-direction:column}.pokesDiv{display:grid;grid-template-columns:repeat(4, 1fr);grid-gap:1em;gap:1em}.pokesDiv .pokeCard{width:100%;padding-top:100%;position:relative}.pokesDiv .pokeCard .flip-card{position:absolute;top:50%;left:50%;width:100%;height:100%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.pokesDiv .pokeCard *{font-size:1vw}.pokesDiv .pokeCard .name{position:absolute;bottom:1vw}.img-wrapper{width:100%;padding-top:100%}.img-wrapper img{width:100%;height:100%;position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.pagination{width:100%}.pagination button{background:var(--red);border:0;margin:.5em;color:#fff;border-radius:50px;padding:1em;outline:none;cursor:pointer;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%);position:absolute}.pagination button:hover{background:var(--yellow);transition:all .2s}.pagination button:before{content:'';display:block;border:5px solid #fff;width:2vw;height:2vw;-webkit-transform:rotate(45deg);transform:rotate(45deg);border-top:0;border-right:0}.pagination button.next:before{-webkit-transform:rotate(230deg);transform:rotate(230deg)}.pagination .next{left:100%}.pagination .prev{left:-4.2em}.top-div-active{background:#000;flex-direction:column;height:100%;margin-bottom:0 !important}.top-div-active .teamDiv{width:100%;height:37%;align-items:center;flex-direction:column}.top-div-active .teamDiv *{font-size:1.2vw;margin-bottom:1vw}.top-div-active .teamDiv h2{font-size:2vw}.top-div-active .teamDiv .cards-div{width:100%;justify-content:space-around}.teamDiv-img-active{width:12em !important;transition:all 1.2s}.winlose-div{-webkit-animation:showup 1.6s;animation:showup 1.6s;background:rgba(255,255,255,0.15);width:100%;height:100%;position:absolute;display:flex;flex-direction:column;align-items:center;justify-content:center;left:0}@-webkit-keyframes showup{from{opacity:0}to{opacity:1}}@keyframes showup{from{opacity:0}to{opacity:1}}.scroll-active{overflow-y:scroll;padding:0 1em 0 0;max-height:70vh}@media only screen and (max-device-width: 900px){.top-div-active .teamDiv{flex-direction:column;height:60% !important}}

.ScoreSheet{text-align:center;width:90%;margin:0 auto;align-self:center;height:100vh;display:flex;flex-direction:column;justify-content:center}.ScoreSheet h1,.ScoreSheet h2{color:#fff;-webkit-text-stroke:1px black}.ScoreSheet h1{font-size:1.6vw}.ScoreSheet i{font-size:2.6vw}.ScoreSheet .table{margin-top:1vw;border-radius:11px;max-height:70vh}.ScoreSheet .table ul:not(.data-div){display:grid;grid-template-columns:1.6fr 1fr 1fr 1fr 1fr 1fr;grid-gap:.25em;gap:.25em;margin-bottom:.25em;text-transform:uppercase}.ScoreSheet .table .data-div{height:47vh;overflow-y:scroll;padding:0 .5em 0 0;width:calc(100% + .5em + 20px)}.ScoreSheet .table li{background:var(--yellow);border-radius:5px;display:flex;justify-content:center;align-items:center;width:100%;height:100%;padding:1.5em 0}.ScoreSheet .table .top-div li{background:#d1a81f}.ScoreSheet .table .top-div .score{grid-column:3/5}.ScoreSheet .table .top-div .win-lose{grid-column:5/6;grid-row:1/3}.ScoreSheet .table .top-div .detail{grid-column:6/7;grid-row:1/3}.ScoreDetail{width:100%;height:100vh;display:flex}.ScoreDetail img{width:6vw}.detail-frame{text-align:center;background:var(--red);border-radius:15px;max-width:1100px;margin:0 auto;padding:2em;align-self:center;position:relative;width:90%;font-size:1vw}.detail-frame>h1{margin-bottom:1vh;font-size:1.6vw}.detail-frame section{display:flex;justify-content:space-around}.detail-frame section .members{display:flex;flex-direction:column;width:47%;border-radius:11px}.detail-frame section .members .member{background:#fff;border-radius:11px;display:flex;margin-bottom:.5em;padding:.5em;align-items:center;justify-content:space-evenly;margin:1vh 0}.detail-frame section .members .member .info-div{text-align:left;text-align:initial}.detail-frame section .members .member h1{font-size:1.2vw}.detail-frame .index-btn{font-size:2.6vw;padding:.5em;margin:1vw}.detail-frame .fa-trophy{font-size:5em;color:gold;position:absolute;left:1.5em;top:0;-webkit-transform:rotate(320deg);transform:rotate(320deg)}.fa-trophy{color:var(--red)}.fa-tired{color:green}@media only screen and (max-device-width: 900px){.ScoreSheet .table li{padding:.72em 0}.ScoreSheet .table{margin-top:.5em}.detail-frame section .members .member{padding:0}}

