@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Press Start 2P', 'Montserrat', sans-serif;
  // font-family: 'Montserrat', sans-serif;
  box-sizing: border-box;
}
:root{
  --red: #fe5350;
  --yellow: rgb(254, 202, 27);
  --black: #14171c;
  --lightbg: rgba(0, 0, 0, 0.1)
}
// :root {
//   $red: #fe5350;
//   $yellow: rgb(254, 202, 27);
//   $black: #14171c;
//   $lightbg: rgba(0, 0, 0, 0.1);
// }
.GameApp {
    margin: 0 auto;
    box-sizing: border-box;
    z-index: -1;
    display: flex;
    width: 100%;
    min-height: 100vh;
    height: 100%;
}
.bg {
  // min-width: 1200px;
  // min-height: 520px;
  // min-height: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  // background-attachment: fixed;
  margin: 0 auto;
  width: 100%;
  height: 100%;
  // padding-top: 1.5em;
  // height: 100vh;
  position: absolute;
  z-index: -1;

  img {
    width: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: none;
  }
}
li {
  list-style: none;
  padding: 1em 1.5em;
  font-size: 1.6vw;
}

/* custom scrollbar^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^ */
::-webkit-scrollbar {
  width: 20px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px ; 
  border-radius: 10px;
  background: rgba(0, 0, 0, 0.1)
}
::-webkit-scrollbar-thumb {
  background: var(--yellow); 
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--red); 
}
.block {
  display: block !important;
}

/* loader^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^ */
.loader-wrapper {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid var(--red);
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite; /* Safari */
  animation: spin 2s linear infinite;
}

/* Safari */
@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
/* loader^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^^ */



.startBtn {
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: transparent;
  border-radius: 0.6em;
  cursor: pointer;
  align-self: center;
  line-height: 1;
  padding: 3.2vw;
  text-decoration: none;
  text-align: center;
  text-transform: uppercase;
  // font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  border-color: var(--red);
  color: #fff;
  box-shadow: 0 0 40px 40px var(--red) inset, 0 0 0 0 var(--red);
  transition: all 350ms ease-in-out;
  animation: shine .5s infinite;
  display: flex;
  justify-content: center;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 999;
  font-size: 2vw;
}
.startBtn:hover,
.startBtn:focus {
  outline: 0;
}
.startBtn:hover {
  box-shadow: 0 0 10px 0 var(--red) inset, 0 0 10px 4px var(--red);
  color: var(--red) !important;
}
@keyframes shine {
  form {}
  to {
    box-shadow: 0 0 10px 0 var(--red) inset, 0 0 10px 4px var(--red);
    color: var(--red) !important;
  }
}
.teamDiv {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
  img {
    width: 5vw;
  }
}
