.text-shadow {
  color: var(--red);
  text-shadow: 1px 1px 2px black, 0 0 25px var(--yellow), 0 0 5px var(--yellow);
  text-align: center;
  letter-spacing: 5px;
  color: var(--red);
  animation: textshine .5s infinite;
  margin-bottom: 1vw;
  font-size: 3.7vw;
}
@keyframes textshine {
  form {}
  to {
    text-shadow: 1px 1px 2px var(--yellow), 0 0 25px #fff, 0 0 5px #fff;
  }
}


.replayBtn {
  background: var(--red);
  border-radius: 50%;
  width: 6vw;
  height: 6vw;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
    i {
      font-size: 3.2vw;
      font-weight: 900;
      color: #fff;
      animation: spin 2s linear;
  }
  &:hover {
    background: var(--yellow);
    outline: none;
  }
}
@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(270deg); }
}

.rotate-div {
  width: 100%;
  height: 100%;
  background: var(--red);
  display: none;
}







.small-btn {
  text-shadow: 1px 1px pink, -1px -1px maroon;
  font-size: 22pt;
  width: 1.5em;
  border-radius: .75em;
  background-color: var(--red);
  -o-box-shadow: 0 .2em maroon;
  box-shadow: 0 0.2em maroon;
  color: var(--red);
  background-color: var(--red);
  background-image: linear-gradient(left top, pink 3%, var(--red) 22%, maroon 99%);
  cursor: pointer;
  border-radius: 50%;
  width: 10px;
  display: flex;
  justify-content: center;
  margin: 0 .5rem;
}
.small-btn:active, .index-btn:active {
    box-shadow: none;
    position: relative;
    top: .2em;
}

.index-btn{
  text-align: center;
  font-weight: bold;
  padding: 3.2vw;
  text-shadow: -1px -1px black, 1px 1px white;
  color: gray;
  border-radius: 7px;
  box-shadow: 0 0.2em grey;
  cursor: pointer;
  background: lightgrey;
}

