.SearchBar {
    width: fit-content;
    position: relative;
    margin: .15em;
    &:after {
      content: "";
      background: #fff;
      width: 4px;
      height: 2.5vw;
      position: absolute;
      top: 3vw;
      right: -9px;
      transform: rotate(115deg);
      box-shadow: 1px 0 var(--red);
    }
    input {
      color: #fff;
    font-size: 16px;
    background: transparent;
    width: 5vw;
    height: 5vw;
    /* padding-left: 1.5em; */
    border: solid 3px;
    outline: none;
    border-radius: 50%;
    box-shadow: 0 1px var(--red);
    transition: width 0.5s;
    background: rgba(255, 255, 255, 0.25);
    }
  }
  
  .SearchBar:after {
    content: "";
    background: #fff;
    width: 4px;
    height: 2.5vw;
    position: absolute;
    top: 3vw;
    right: -7px;
    transform: rotate(115deg);
    box-shadow: 1px 0 var(--red);
  }
