  .SearchBar > input::placeholder {
    color: #efefef;
    opacity: 1;
  }
  
  .SearchBar > input::-ms-placeholder {
    color: #efefef;
  }
  
  .SearchBar > input::-ms-input-placeholder {
    color: #efefef;
  }
  
  .SearchBar > input:focus,
  .SearchBar > input:valid {
    width: 250px;
    border-radius: 37px;
  }

  @media only screen and (max-width: 790px) {
    .SearchBar {
      align-self: flex-start;
      margin-bottom: .5em;
    }
  }